import React from "react"
import * as layoutStyle from "./docslayout.module.css"
import DocLogo from "../doclogo/doclogo"
import {Container} from "react-bootstrap"
import ContentsTable from "../contentstable/contentstable"
import DocsFooter from "../footer/docsfooter"
import {Image} from "react-bootstrap"
import LanguageSelector from "../../common/languageselector/languageselector"
import * as contentsTableData from "../../../data/docs-content-table.json"

const DocsLayout = ({section, children}) => {
	const [state, setState] = React.useState({isSidebarVisible: false})

	const toggleSidebarVisibility = () => {
		setState(prevState => ({isSidebarVisible: !prevState.isSidebarVisible}))
	}

	let {isSidebarVisible} = state
	let sidebarStyle = layoutStyle.sidebar

	if (isSidebarVisible) {
		sidebarStyle += " " + layoutStyle.activeSidebar
	}

	return (
		<>
			<div className={layoutStyle.layout}>
				<header className={layoutStyle.docsHeader}>
					<DocLogo/>
					<LanguageSelector docs={true}/>
					<nav className={layoutStyle.docsMobileHeader}>
						<div className={layoutStyle.sidebarCloseWrap}>
							<Image src="/images/MenuShow.svg" width="50" height="50"
							       onClick={() => toggleSidebarVisibility()}></Image>
						</div>
					</nav>
				</header>
				<aside className={sidebarStyle}>
					<div className={layoutStyle.sidebarCloseWrap}>
						<Image src="/images/MenuClose.svg" width="50" height="50"
						       onClick={() => toggleSidebarVisibility()}></Image>
					</div>
					<ContentsTable contentsTableData={contentsTableData.content} section={section}></ContentsTable>
				</aside>
				<div className={layoutStyle.layoutWrap}>
					<div className={layoutStyle.docsLayout}>
						<Container>
							<h1>{section}</h1>
							<div className={layoutStyle.docsDecorTop}></div>
							<main>
								<article>
									{children}
								</article>
							</main>
						</Container>
						<DocsFooter/>
					</div>
				</div>
			</div>
		</>
	)
}

export default DocsLayout